<template >
<div id="myregisbox">
   <el-form>
            <el-form-item label="名称">
                <el-input type="text" id="user" v-model="formName.username" @change="inputBlur('user',formName.username)"  placeholder="清输入名称"></el-input>
                <p>{{formName.userError}}</p>
            </el-form-item>
            <el-form-item label="密码">
            <el-input type="password" id="password" v-model="formName.password1" @change="inputBlur('password1',formName.password1)"  placeholder="最少6位，包括至少1个大写字母，1个小写字母，1个数字，1个特殊字符"></el-input>
                <p>{{formName.passwordError1}}</p>
            </el-form-item>
            <el-form-item label="再次输入密码">
            <el-input type="password" id="password" v-model="formName.password2" @change="inputBlur('password2',formName.password2)"  placeholder="最少6位，包括至少1个大写字母，1个小写字母，1个数字，1个特殊字符">
            </el-input>
                <p>{{formName.passwordError2}}</p>
                <p>{{different}}</p>
            </el-form-item>
            <el-form-item label="请输入图片上的验证码">
                 <div class="code" @click="refreshCode">
                    <myCanvas :identifyCode="identifyCode"></myCanvas>
                </div>
                <el-input type="text" id="user" v-model="inputcode" @change="inputBlur('icode',inputcode)"  placeholder="验证码"></el-input>
                <p>{{passwordcanvasError}}</p>
                
            </el-form-item>
            <el-button type="primary" @click="submitForm(formName)" v-bind:disabled="formName.beDisabled">提交</el-button>
            <el-button @click="resetForm">重置</el-button>
        </el-form>             
   
  
</div>  
</template>
  

<script>

import myCanvas from './canvas'
export default {
  name: "codetest",
  data() {
    return {
      identifyCodes: "1234567890",
      identifyCode: "",
       username: '',
       flag:false,
       inputcode: '',
       passwordcanvasError: '',
       different:'',
       formName: {
          userError: '',
          password: '',
          password1: '',
          password2: '',
          passwordError1: '',
          passwordError2: '',
          beDisabled: true
        }  
    };
  },
  components: {
    myCanvas
  },
  methods: {
   resetForm:function(){
                this.formName.username = '';
                this.formName.userError = '';
                this.formName.password1 = '';
                this.formName.password2 = '';
                this.formName.passwordError1 = '';
                this.formName.password2 = '';
                this.formName.passwordError2 = '';
                this.passwordcanvasError="";
                 this.inputcode="";
                  this.different="";
    },
    inputBlur:function(errorItem,inputContent){
                if (errorItem === 'user') {
                    if (inputContent === '') {
                        this.formName.userError = '用户名不能为空'
                        this.flag=false
                    }else{
                        this.formName.userError = '';
                    }
                }else if(errorItem === 'password1') {
                    var patrn= /^.*(?=.{6,})(?=.*\d)(?=.*[A-Z])(?=.*[a-z])(?=.*[!@#$%^&*? ]).*$/;
                    var r = inputContent.match(patrn); 
                    if (inputContent === '' |  r==null) {
                        this.formName.passwordError1 = '密码格式不对'
                         this.flag=false
                    }else{
                        
                        this.formName.passwordError1 = '';
                    }
                }else if(errorItem === 'password2') {
                    var patrn= /^.*(?=.{6,})(?=.*\d)(?=.*[A-Z])(?=.*[a-z])(?=.*[!@#$%^&*? ]).*$/;
                    var r = inputContent.match(patrn); 
                    if (inputContent === '' |  r==null) {
                        this.formName.passwordError2 = '密码格式不对'
                        this.flag=false
                    }else{
                      if(this.formName.password1==this.formName.password2){
                           this.formName.password=this.formName.password1
                           
                          this.different=""
                          }else{
                  
                          this.flag=false
                          this.different="两次密码不相同"
                          }
                        this.formName.passwordError2 = '';
                    }
                }else if(errorItem === 'icode') {
                  
                    if (inputContent ==this.identifyCode ) {
                         this.flag=true
                         this.passwordcanvasError = '';
                    }else{
                        
                        this.passwordcanvasError = '验证码错误'
                         this.flag=false
                    }
                }        
                //对于按钮的状态进行修改

                if (this.formName.username != '' && this.formName.password != '' && this.flag==true && this.inputcode!= '') {
                    this.formName.beDisabled = false;
                }else{
                    this.formName.beDisabled = true;
                }
    },
    submitForm:function(formName){
               this.$store.dispatch('resetuser',formName)
               this.$router.push({ name: 'product'})
    },
    randomNum(min, max) {
      return Math.floor(Math.random() * (max - min) + min);
    },
    refreshCode() {
      this.identifyCode = "";
      this.makeCode(this.identifyCodes, 4);
    },
    makeCode(o, l) {
      for (let i = 0; i < l; i++) {
        this.identifyCode += this.identifyCodes[
          this.randomNum(0, this.identifyCodes.length)
        ];
      }
      console.log(this.identifyCode);
    }
  },
  mounted() {
    this.identifyCode = "";
    this.makeCode(this.identifyCodes, 4);
  },
}
</script>

<style scoped>
#myregisbox{
  width:50rem;
  height:auto;
  margin:auto;
}
    
  
</style>


